.lemon__booking-forms_review-group {
  background: var(--Color-Primary);
  display: flex;
  justify-content: space-evenly;
  padding: 1.5rem;
  border-radius: 31px;
  margin-bottom: 1.5rem;
  align-items: center;
}

.lemon__booking-forms_review-left {
  width: 190px;
  height: 240px;
  border-radius: 18px;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.lemon__booking-forms_review-left figcaption {
  background: var(--Color-Highlight-Light);
  border-radius: 0px 0px 18px 18px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 15px;
  color: var(--Color-Primary);
}

.lemon__booking-forms_review-left h4 {
  font-family: var(--Font-Primary);
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
}

.lemon__booking-forms_review-left p {
  font-family: var(--Font-Primary);
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
}

.lemon__booking-forms_review-right {
  font-family: var(--Font-Secondary);
  font-size: 24px;
  line-height: 32px;
  color: white;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  gap: 15px;
}

@media screen and (max-width: 950px) {
  .lemon__booking-forms_review-group {
    flex-direction: column;
    align-items: center;
  }

  .lemon__booking-forms_review-left {
    margin-bottom: 2rem;
    width: 228px;
    height: 288px;
  }

  .lemon__booking-forms_review-left h4 {
    font-size: 24px; /* Increased font size by 20% */
  }

  .lemon__booking-forms_review-left p {
    font-size: 19.2px; /* Increased font size by 20% */
  }

  .lemon__booking-forms_review-right {
    text-align: center;
    align-items: center;
  }

  .lemon__booking-forms_review-right b {
    font-size: 22px;
    line-height: 30px;
    text-align: center;
    /* text-decoration: underline; */
  }
  .lemon__booking-forms_review-right span {
    font-size: 20px;
    line-height: 24px;
    display: block;
  }
}
